import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, message, Col, Checkbox } from 'antd';
import { getGradeConvert, deleteGradeConvert } from 'services/GradeConvert';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DIEM_XEP_LOAI__QUY_DOI_DIEM__NHAP_DU_LIEU;
class RedeemPointsPage extends Component {
  state = {};
  handleDelete = async id => {
    await deleteGradeConvert(id);
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Xếp loại',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Điểm chữ',
        dataIndex: 'gradeInLetter',
        key: 'gradeInLetter',
      },
      {
        title: 'Từ thang điểm 4',
        dataIndex: 'fromGradeIn4',
        key: 'fromGradeIn4',
      },
      {
        title: 'Đến thang điểm 4',
        dataIndex: 'toGradeIn4',
        key: 'toGradeIn4',
      },
      {
        title: 'Từ thang điểm 10',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'Đến thang điểm 10',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Tích lũy',
        dataIndex: 'isCollective',
        key: 'isCollective',
        render: (text, record) => <Checkbox checked={record.isCollective} />,
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Quy đổi điểm">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getGradeConvert}
              columns={columns}
              rowKey={record => record.label + record.id}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RedeemPointsPage />
  </Page>
);



